<template>
    <div>
        <van-pull-refresh
                v-model="isLoading"
                success-text="刷新成功"
                @refresh="onRefresh"
        >
            <div>
                <div style="position: fixed;width: 26rem;z-index: 999999">
                    <van-nav-bar
                            :title="'搜索结果：'+vod_name"
                            left-text="返回"
                            left-arrow
                            @click-left="onClickLeft"
                    />
                </div>
                <div style="padding-top: 4rem;margin-left: 0.6rem">
                    <van-empty v-if="list.length==0" description="暂无数据"/>
                    <div class="neiDiv" v-if="list.length!=0">
                        <van-row v-for="(item,index) in list" v-bind:key="item.vod_id">
                            <div v-if="index<list.length&&index%2==0">
                                <van-col span="12">
                                    <div class="myimgSpanDiv">
                                        <van-image lazy-load class="my_img" :src="list[index].vod_pic"
                                                   alt=""></van-image>
                                        <br>
                                        <span>{{list[index].vod_name}}</span>
                                    </div>
                                </van-col>
                                <van-col span="12" v-if="index<list.length-1">
                                    <div class="myimgSpanDiv">
                                        <van-image lazy-load class="my_img" :src="list[index+1].vod_pic"
                                                   alt=""></van-image>
                                        <br>
                                        <span>{{list[index+1].vod_name}}</span>
                                    </div>
                                </van-col>
                            </div>
                        </van-row>
                    </div>
                </div>
            </div>
        </van-pull-refresh>
    </div>
</template>
<style>
    .neiDiv {
        margin-left: 1.8rem
    }
    .neiDiv span {
        display: -webkit-box; /** 将对象作为伸缩盒子模型显示 **/
        -webkit-box-orient: vertical; /** 设置或检索伸缩盒对象的子元素的排列方式 **/
        -webkit-line-clamp: 1; /** 显示的行数，显示高度设置成行高，多余部分隐藏 **/
        overflow: hidden; /**超出部分隐藏 **/
        width: 9rem;
    }

    .myimgSpanDiv {
        width: 9rem;
    }

    .van-row {
        margin-bottom: 0.5rem;
    }
</style>
<script>
    import $ from "jquery";
    export default {
        data() {
            return {
                vod_name:'',
                isLoading:false,
                list: [],
            }
        },
        created() {
            this.vod_name = this.$route.query.vod_name;
            this.getData()
        },
        methods: {
            getData(){
                let _this=this;
                $.ajax({
                    url: _this.baseUrl + "/user/search",
                    data: {vod_name:_this.vod_name},
                    type: 'post',
                    traditional: true,
                    async: true,
                    dataType: "json",
                    success: function (data) {
                        _this.list=data.data;
                        _this.isLoading=false;
                    }
                });
            },
            onClickLeft() {
                this.$router.push({path: '/home'});
            },
            onRefresh() {
                let _this=this;
                setTimeout(function () {
                    _this.getData()
                },2000)

            },
        },
    };
</script>
